:root{
    --theme_color: #c98895; /*db98a5 #ce8896 */
    --font_type: 'Alice'
}

body{
    /* background-color: #c98895; */
    margin : 0;
    padding : 0;
    font-family: var(--font_type);
}

.header{
    font-size: 3vw;
    color: var(--theme_color);
    padding-top: 20px;
}

.form-control , .form-select{
    border-color: var(--theme_color);    
}

.form-control:focus ,.form-select:focus {
    border-color: var(--theme_color);
    box-shadow: inset 1px 1px 1px #d4cacc, 1px 1px 8px #ce8896; 
}

.lbl{
    color: var(--theme_color);
}

.meanings{
    border: 1px solid #ce8896;
    border-radius: 10px;
    padding: 1em;
    /* background-color: #ce8896; */
    /* color: white */
}

.definitions{
    color: white;
    font-family: var(--font_type);
    background-color: #ce8896;
    border: 1px solid #ce8896;
    border-radius: 10px;
}

